import { Component, OnInit, Input } from '@angular/core';
import { OrderService } from '../../service/order.service';
import { FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'app-indent-filter',
  templateUrl: './indent-filter.component.html',
  styleUrls: ['./indent-filter.component.scss'],
})
export class IndentFilterComponent implements OnInit {

  openMenu: Boolean = false;
  filterType: string;
  productDropdown: any = [];
  categoryDropdown: any = [];
  agencyDropdown: any = [];
  districtDropdown: any = [];
  placeDropdown: any = [];
  stateDropdown: any = [];

  filterForm: FormGroup;
  numselecteditem = 0;
  orderFilterForm = {
    category: [],
    product: [],
    fromdate: '',
    todate: '',
    district: [],
    place: [],
    agency: [],
    fieldagent: []
  };

  ports = [
    { id: 0, name: 'Tokai', country: 'Japan' },
    { id: 2, name: 'Vladivostok', country: 'Russia' },
    { id: 3, name: 'Navlakhi', country: 'India' },
    { id: 4, name: 'Cayman Brac', country: 'Cayman Islands' }];
  port3: any;
  constructor(private orderService: OrderService,
    // private nav:NavController,
    // private modalController: ModalController,
  ) {
    this.orderService.getProducts().subscribe(res => {
      console.log('>>>>>>>>>>>>', res);
      this.productDropdown = res.product_name;
      this.categoryDropdown = res.category_name;
      this.agencyDropdown = res.agency_name;
      this.districtDropdown = res.district;
      this.stateDropdown = res.state;
      this.placeDropdown = res.place;
    });

    this.orderService._indent_list.subscribe(dat => {
      this.filterType = dat;
    });
    //  console.log('filterType', this.filterType);
  }

  ngOnInit() {
    this.filterForm = new FormGroup({
      category: new FormControl(),
      product: new FormControl(),
      // fromdate: new FormControl(),
      start_date: new FormControl(),
      end_date: new FormControl(),
      district: new FormControl(),
      place: new FormControl(),
      agency: new FormControl(),
      // fieldagent: new FormControl()
    });
  }



  submitFilter(val) {
    console.log('values', val);
    Object.keys(val).forEach(key => {
      val[key].length === 0 ? delete val[key] : key;
      val[key] == "" ? delete val[key] : key
    });
    this.orderService.filterIndent(val);
    this.openMenu = false;

  }
  checkselection(e, t) {
    // let tmpnum = 0;
    // console.log(e);
    if (t === 'category') {

      this.orderFilterForm.category = e;

    } else if (t === 'product') {

      this.orderFilterForm.product = e;

    } else if (t === 'fromdate') {

      this.orderFilterForm.fromdate = e;

    } else if (t === 'todate') {

      this.orderFilterForm.todate = e;

    } else if (t === 'district') {

      this.orderFilterForm.district = e;

    } else if (t === 'place') {

      this.orderFilterForm.place = e;

    } else if (t === 'agency') {

      this.orderFilterForm.agency = e;

    } else if (t === 'fieldagent') {

      this.orderFilterForm.fieldagent = e;

    }

    this.numselecteditem = this.orderFilterForm.category.length;
    this.numselecteditem = this.numselecteditem + this.orderFilterForm.product.length;
    this.numselecteditem = this.numselecteditem + this.orderFilterForm.district.length;
    this.numselecteditem = this.numselecteditem + this.orderFilterForm.place.length;
    this.numselecteditem = this.numselecteditem + this.orderFilterForm.agency.length;
    this.numselecteditem = this.numselecteditem + this.orderFilterForm.fieldagent.length;

    if (this.orderFilterForm.fromdate) {
      this.numselecteditem = this.numselecteditem + 1;
    }

    if (this.orderFilterForm.todate) {
      this.numselecteditem = this.numselecteditem + 1;
    }

    if (this.numselecteditem) {
      console.log(this.numselecteditem);
    }
  }


  deleteSelected(t, val) {

    if (t === 'category') {
      const i = this.orderFilterForm.category.findIndex(v => v === val);
      if (i !== -1) {
        this.orderFilterForm.category.splice(i, 1);
      }

    } else if (t === 'product') {
      const i = this.orderFilterForm.product.findIndex(v => v === val);
      if (i !== -1) {
        this.orderFilterForm.product.splice(i, 1);
      }

    } else if (t === 'fromdate') {

      this.orderFilterForm.fromdate = '';

    } else if (t === 'todate') {

      this.orderFilterForm.todate = '';

    } else if (t === 'district') {

      const i = this.orderFilterForm.district.findIndex(v => v === val);
      if (i !== -1) {
        this.orderFilterForm.district.splice(i, 1);
      }

    } else if (t === 'place') {

      const i = this.orderFilterForm.place.findIndex(v => v === val);
      if (i !== -1) {
        this.orderFilterForm.place.splice(i, 1);
      }

    } else if (t === 'agency') {

      const i = this.orderFilterForm.agency.findIndex(v => v === val);
      if (i !== -1) {
        this.orderFilterForm.agency.splice(i, 1);
      }

    } else if (t === 'fieldagent') {

      const i = this.orderFilterForm.fieldagent.findIndex(v => v === val);
      if (i !== -1) {
        this.orderFilterForm.fieldagent.splice(i, 1);
      }

    }

    this.filterForm.setValue({
      category: this.orderFilterForm.category,
      product: this.orderFilterForm.product,
      fromdate: this.orderFilterForm.fromdate,
      todate: this.orderFilterForm.todate,
      district: this.orderFilterForm.district,
      place: this.orderFilterForm.place,
      agency: this.orderFilterForm.agency,
      // fieldagent: this.orderFilterForm.fieldagent
    });

    console.log(this.filterForm.value);
  }

  togglePopupMenu() {

    return this.openMenu = !this.openMenu;
  }
}